    margin: 0 auto;
  }
  .about-section {
    flex-direction: column;
  }
  .profile-img {
    max-width: 80vw;
    min-width: 80vw;
  }
  .about-image {
    padding-left: 1em;
    text-align: left;
  }
  .about-text {
    padding-right: 1.5em;
    padding-left: 1em;
  }
  .section-container {
    flex-direction: column;
  }
}

