.nav {
  width: 100%;
  height: 1em;
  display: flex;
  flex-direction: row;
}
.nav:hover {
  cursor: pointer;
}
.nav-link {
  width: 100%;
  height: 100%;
}
.nav-link:hover {
  cursor: pointer;
}
.work-tab {
  flex: 1 1;
  background-color: #EDFD5D;
}
.about-tab {
  flex: 1 1;
  background-color: #70DBFB;
  /* background-color: #F0913C; */
}
.contact-tab {
  flex: 1 1;
  background-color: #FF7DEE;
}
.work-section {
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  background-color: #EDFD5D;
  padding-top: 3em;
}

.landing {
  font-family: 'Ubuntu Mono', monospace;
  text-align: left;
  background-color: black;
  color: white;
  height: auto;
  width: auto;
  display: flex;
  flex-direction: column;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
.landing-top {
  display: flex;
  flex-direction: row;
  padding-left: 1em;
}
.landing-bottom {
  padding-left: 1em;
  padding-bottom: .5em;
  display: flex;
  flex-direction: row;
}
.landing-bottom-left {
  display: flex;
  align-items: center;
  flex: 1 1;
  padding-top: .2em;
}
.landing-bottom-right {
  display: flex;
}
.landing-left {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.landing-left:hover {
  cursor: default;
}
.landing-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex: 1 1;
}
.landing h1 {
  font-size: 23vw;
  -webkit-transition: font-size 0.5s ease-in-out;
  transition: font-size 0.5s ease-in-out;
}
.landing-bottom h3 {
  font-size: 10vw;
  -webkit-transition: font-size 0.5s ease-in-out;
  transition: font-size 0.5s ease-in-out;
}
.landing-bottom h2 {
  margin-right: .3em;
  font-size: 6vw;
  -webkit-transition: font-size 0.5s ease-in-out;
  transition: font-size 0.5s ease-in-out;
}
.landing-bottom h2:hover {
  cursor: pointer;
}

.work-section {
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  background-color: #EDFD5D;
  padding-top: 3em;
}
.project-section {
  width: 100vw;
  background-color: #EDFD5D;
  padding-top: 3em;
}
.project-preview {
  display: flex;
  flex-direction: column;
  margin-left: 1em;
  margin-right: 1em;
  margin-bottom: 3em;
  flex: 1 1;
  position: relative;
}
.project-preview a {
  color: black;
}
.overlay {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  display: flex;
  flex-direction: row;
  background-color: rgba(255,255,255,0.9);
  border: 1px solid rgba(255,255,255,0.9);
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
  font-size: 2.5vw;
}
.overlay:hover {
  opacity: 1;
  cursor: pointer;
}
.overlay:hover ~ .under {
  border-color: rgba(255,255,255,0.9);
}
.overlay-img {
  opacity: 0;
}
.overlay-h2 {
  opacity: 0;
}
.under {
  border: 1px solid black;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.project-preview:hover {
  cursor: pointer;
}
.project-preview-image img {
  display:block;
  width:100%;
}
.project-preview-image  {
  width: 300px;
  overflow:hidden;
  margin:0;
}
.project-preview-title {
  flex: 1 1;
  min-width: 13vw;
  height: 144.39px;
  padding: .5em;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.overlay, .project-preview-title {
  font-size: 1em;
}
.project-preview-title h1 {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: .5em;
}
.project-preview-image {
  -webkit-transition: all .3s ease-in;
  transition: all .3s ease-in;
}

.about-section {
  width: 100vw;
  background-color: #70DBFB;
  padding-top: 3em;
}
.about-section {
  display: flex;
  flex-direction: row;
  flex:1 1;
  -webkit-transition: all .3s ease-in;
  transition: all .3s ease-in;
}
.about-section-div {
  display: flex;
  flex-direction: row;
}
.about-section .project-skills {
  display: flex;
  flex-direction: row;
}
.about-text {
  flex: 1 1;
  padding-right: 0;
  padding-left: 1em;
}
.about-text a {
  font-weight: bold;
}
.about-text p {
  margin-bottom: 1em;
}
.about-image {
  padding: 0 2em;
  /* text-align: center; */
}
.profile-img {
  max-width: 40vw;
  min-width: 40vw;
}
@media only screen and (max-width : 650px) {
  .about-section-div {
    display: flex;
    flex-direction: column;
  }
  .project-links {
    margin-bottom: 2em;
  }
  .about-image {
    padding: 0 1em;
    text-align: left;
  }
  .profile-img {
    min-width: 70vw;
  }
  .about-text {
    flex: 0 1;
    padding-right: 3em;
  }
}

.contact-section {
  display: flex;
  flex-direction: column;
  width: 100vw;
  background-color: #FF7DEE;
  padding-top: 1em;
}
.contact-section-title {
  display: flex;
  flex-direction: row;
}
.contact-section-title h1 {
  margin-bottom: .5em;
  font-size: 3vw;
}
.form-section {
  width: 60vw;
}
form {
  width: 100%;
}
.form-section-inputs input {
  font-size: 1.5em;
  padding: .3em;
  margin-bottom: 1em;
  border-style: none;
  border: 1px solid black;
  background-color: #FF7DEE;
  font-family: 'Ubuntu Mono', monospace;
}
textarea {
  height: 10vw;
  font-size: 2vw;
  background-color: #FF7DEE;
  font-family: 'Ubuntu Mono', monospace;
  border: 1px solid black;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}
.form-section-inputs {
  display: flex;
  flex-direction: column;
  margin-left: 1em;
  margin-bottom: 1em;
}
.form-section-submit {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 1em;
}
.form-section-submit input {
  font-size: 1em;
  margin-bottom: 2em;
  padding:  .3em .5em;
}
label {
  margin-bottom: .5em;
  font-weight: bold;
  font-size: 1em;
}
.submit {
  font-family: 'Ubuntu Mono', monospace;
  font-weight: bold;
  background-color: #FF7DEE;
  border: 1px solid black;
  padding: .3em .5em;
  text-align: center;
  width: 10em;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
  color: black;
}
.submit:hover {
  cursor: pointer;
  background-color: rgba(255,255,255,0.9);
  border-color: white;
}

@-webkit-keyframes blink {
    0% {
      opacity: .2;
    }
    20% {
      opacity: 1;
    }
    100% {
      opacity: .2;
    }
}

@keyframes blink {
    0% {
      opacity: .2;
    }
    20% {
      opacity: 1;
    }
    100% {
      opacity: .2;
    }
}
.submit-loading {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.submit-loading div {
    -webkit-animation-name: blink;
            animation-name: blink;
    -webkit-animation-duration: 1.4s;
            animation-duration: 1.4s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

.submit div:nth-child(2) {
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}

.submit div:nth-child(3) {
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
}
.sub {
  margin-bottom: 2em;
}
.response {
  text-align: center;
  margin: 0 auto;
  margin-bottom: 1em;
  background-color: rgba(255,255,255,0.9);
  padding: .3em 1em;
  font-weight: bold;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}

.project {
  width: 100vw;
  display: flex;
}
.project-info {
  flex: 1 1;
  padding-left: 1em;
}
.project-images {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  padding: 0 1em;
}
.project-images img {
  padding: 0 1em;
  margin-bottom: 3em;
  max-width: 50vw;
}
.project-section a {
  color: black;
  font-weight: bold;
}
.project-description {
  flex: 1 1;
  margin-bottom: 1em;
}
.project-skills {
  font-weight: bold;
  margin-bottom: 1em;
}
.project-links {
  display: flex;
  flex-direction: column;
}
.project-links div {
  border: 1px solid black;
  padding: .3em .5em;
  text-align: center;
  margin-bottom: 1em;
  width: 10em;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
  color: black;
}
.project-links div:hover {
  cursor: pointer;
  background-color: rgba(255,255,255,0.9);
  color: black;
  border-color: white;
}
